// widgets spacing
$widget-bottom-margin: 45px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// widget color
$widget-background-color: transparent;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #999999;

// links
$widget-link-color: #eeeeee;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

footer {
    @import 'widgets/default-wordpress-widgets-flat';

    .edgtf-footer-bottom-holder {

        .widget {
            margin-bottom: 0;
        }

        .widget.widget_nav_menu {

            .menu-playerx-footer-menu-container {
                position: relative;

                &:after {
                    content: '';
                    width: calc(100% + 8px);
                    position: absolute;
                    bottom: 0;
                    left: -4px;
                    border-bottom: 5px solid #000;
                }

                li {
                    display: inline-block;
                    overflow: visible;
                    border-bottom: none;
                    padding: 27px 0 47px;

                    a {
                        padding: 30px 32px 50px;
                        margin: 0 3px;
                        font-size: 13px;
                        font-weight: 700;
                        text-transform: uppercase;
                        letter-spacing: 0.06em;

                        &:after {
                            content: '';
                            position: absolute;
                            z-index: 30;
                            bottom: 0;
                            left: 0;
                            height: 5px;
                            width: 100%;
                            background-color: $first-main-color;
                            opacity: 0;
                            @include edgtfTransition(opacity 1s $aux-easing);
                            @include edgtfTransform(skewX(15deg));
                        }

                        &:hover {
                            &:after {
                                opacity: 1;
                                @include edgtfTransition(opacity 1s $default-easing);
                            }
                        }

                    }
                }
            }
        }

        .widget.widget_media_image img {
            margin: 0 auto;
        }
    }
}