/* #Reset default styles - start
   ========================================================================== */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font,
ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    background: transparent;
    border: 0;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    outline: none;
}

article, aside, details, figcaption, figure, footer, header, nav, section {
    display: block;
}

del {
    text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
    border: medium none;
    vertical-align: middle;
}

table th {
    border: 0;
    padding: 5px 7px;
    text-align: center;
}

table td {
    padding: 5px 10px;
    text-align: center;
}

a img {
    border: none;
}

img, .wp-caption {
    max-width: 100%;
    height: auto;
}

iframe {
    max-width: 100%;
}

/* #Reset default styles - end
   ========================================================================== */

/* #General styles that are used in our theme - start
 * Here are defined global styles on body element, on container elements that hold every page,
 * headings and links, inputs, core wp styles and default pagination styles
   ========================================================================== */

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

.clearfix {
    *zoom: 1;
}

.clear:after {
    clear: both;
    content: "";
    display: block;
}

html {
    overflow-y: scroll !important; /* for vertical split screen slider shortcode on touch devices */
}

body {
    font-family: $default-text-font;
    font-size: 15px;
    line-height: 26px;
    font-weight: 300;
    color: $default-text-color;
    background-color: $default-background-color;
    overflow-x: hidden !important;
    -webkit-font-smoothing: antialiased;
}

h1 {
    font-size: 80px;
    line-height: 1.05em;
}

h2 {
    font-size: 54px;
    line-height: 1.11em;
}

h3 {
    font-size: 45px;
    line-height: 1.13em;
}

h4 {
    font-size: 35px;
    line-height: 1.17em;
}

h5 {
    font-size: 22px;
    line-height: 1.36em;
}

h6 {
	font-size: 17px;
    line-height: 1.23em;
    letter-spacing: 0.02em;
}

h1, h2, h3, h4, h5, h6 {
    margin: 20px 0;
    color: $default-heading-color;
    font-weight: 700;
    text-transform: uppercase;
    
    a {
        @include edgtfTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

a,
p a {
    color: $default-heading-color;
    text-decoration: none;
    @include edgtfTransition(color .2s ease-out);

    &:hover {
        color: $first-main-color;
    }
}

p {
    margin: 10px 0;
}

body::-webkit-scrollbar {
    width: 5px;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
    background-color: $first-main-color;
    outline: 1px solid $first-main-color;
}

input[type='submit'] {
    -webkit-appearance: none;
}

ul, ol {
    list-style-position: inside;
    margin: 15px 0;
}

ul ul,
ol ol,
ol ul,
ul ol {
    margin: 0;
    padding: 0 0 0 15px;
}

pre {
    background-color: $default-background-color;
    padding: 15px;
    max-width: 100%;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 15px 0;
}

dt {
    font-size: 15px;
    font-weight: bold;
    margin-bottom: 10px;
}

dd {
    margin-bottom: 15px;
}

sub, sup {
    font-size: 75%;
    height: 0;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sub {
    top: 0.5ex;
}

sup {
    bottom: 0.5ex;
}

.wp-caption-text {
    margin: 0;
    font-style: italic;
}

table {
    width: 100%;
}

table thead tr, table tbody tr {
    border: 1px solid $default-border-color;
}

table tbody th {
    border-right: 1px solid $default-border-color;
}

blockquote {
    @include edgtfRelativeHolderLayout();
    padding: 20px 28px 23px;
    font-size: 17px;
    line-height: 30px;
    text-align: left;
    quotes: none;
    box-sizing: border-box;
    text-transform: uppercase;
    color: $default-text-color;
    background-color: #1c1c1c;

    > * {
        @include edgtfTypographyLayout();
        margin: 0;
    }

    &:before,
    &:after {
        content: '';
    }

	cite {
		font-style: normal;
		font-weight: 400;
	}
}

/* Webkit */
::selection {
    background: $first-main-color;
    color: $default-background-color;
}

/* Gecko/Mozilla */
::-moz-selection {
    background: $first-main-color;
    color: $default-background-color;
}

.wp-caption,
.wp-caption-text,
.sticky,
.gallery-caption,
.bypostauthor {
    opacity: 1;
}

.alignleft {
    display: inline;
    float: left;
    margin: 0 20px 20px 0;
}

.alignright {
    display: inline;
    float: right;
    margin: 0 0 20px 20px;
}

.aligncenter {
    clear: both;
    display: block;
    margin: 10px auto;
}

.alignwide {
    margin-left: auto;
    margin-right: auto;
    clear: both;
}

@include ipad-landscape {
    .alignwide {
        width: 100%;
        max-width: 100%;
    }
}

.alignfull {
    position: relative;
    left: -1rem;
    width: calc(100% + (2 * 1rem));
    max-width: calc(100% + (2 * 1rem));
    clear: both;
}

@include ipad-landscape {
    .alignfull {
        left: calc(-12.5% - 75px);
        width: calc(125% + 150px);
        max-width: calc(125% + 150px);
        margin-top: calc(2 * 1rem);
        margin-bottom: calc(2 * 1rem);
    }
}

.text-align-right {
    text-align: right;
}

.text-align-left {
    text-align: left;
}

.text-align-center {
    text-align: center;
}

.edgtf-wrapper {
    position: relative;
    left: 0;
    z-index: 1000;
    @include edgtfTransition(left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1));

    .edgtf-wrapper-inner {
        width: 100%;
        overflow: hidden;
    }
}

.edgtf-content {
    position: relative;
    margin-top: 0;
    z-index: 100;
    background-color: $default-background-color; /*this is needed because of the uncovering footer*/
}

/* page custom css class for elementor demo */
.edgtf-hide-background-tablet {

    &.edgtf-side-menu-slide-from-right {

        @include ipad-landscape {

            .edgtf-wrapper {
                background-color: transparent;
            }
        }
    }

    @include ipad-landscape {

        .edgtf-content {
            background-image: none !important;
        }
    }
}

.edgtf-boxed {
    overflow: hidden !important; /* this is because content jumping while using accordion and display:inline-block on 'body.edgtf-boxed .edgtf-wrapper'   */

    .edgtf-wrapper {
        display: inline-block;
        vertical-align: middle;
        width: 100%;
    
        .touch & {
            background-repeat: repeat;
        }
        
        .edgtf-wrapper-inner {
            width: $grid-width-boxed;
            overflow: visible;
            margin: auto;
        }
    }

    .edgtf-content {
        overflow: hidden;
    }

    .edgtf-footer-inner {
        width: $grid-width-boxed;
        margin: auto;
    }

    .edgtf-page-header .edgtf-sticky-header,
    .edgtf-page-header .edgtf-fixed-wrapper.fixed {
        width: $grid-width-boxed;
        left: auto;
    }
}

.edgtf-paspartu-enabled {

    .edgtf-wrapper {
        padding: 10px;
        background-color: $default-background-color;
        box-sizing: border-box;
    }

    &.edgtf-top-paspartu-disabled {

        .edgtf-wrapper {
            padding-top: 0 !important;
        }
    }
	
	.edgtf-page-header {
		
		.edgtf-fixed-wrapper {
			
			&.fixed {
				left: 10px;
				width: calc(100% - 20px);
			}
		}
	}
	
	.edgtf-sticky-header {
		left: 10px;
		width: calc(100% - 20px);
	}
	
	.edgtf-mobile-header {
		
		&.mobile-header-appear {
			
			.edgtf-mobile-header-inner {
				left: 10px;
				width: calc(100% - 20px);
			}
		}
	}
    
    &.edgtf-fixed-paspartu-enabled {
        
        &.admin-bar {
    
            .edgtf-wrapper {
                
                &:before {
                    top: 32px;
                }
            }
        }
    
        .edgtf-wrapper {
            
            &:before,
            &:after {
                content: '';
                position: fixed;
                left: 0;
                width: 100%;
                height: auto;
                padding-top: inherit;
                background-color: inherit;
                z-index: 99999;
            }
    
            &:before {
                top: 0;
            }
    
            &:after {
                bottom: 0;
            }
        }
    
        .edgtf-page-header {
            
            .edgtf-fixed-wrapper {
                
                &.fixed {
                    margin-top: 10px;
                }
            }
        }
    
        .edgtf-sticky-header {
        
            &.header-appear {
                margin-top: 10px;
            }
        }
	
	    .edgtf-mobile-header {
		    
		    &.mobile-header-appear {
			    
			    .edgtf-mobile-header-inner {
				    margin-top: 10px;
			    }
		    }
	    }
    }
}

.edgtf-smooth-transition-loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    background-color: #fff;
}

.edgtf-container {
    position: relative;
    padding: 0;
    width: 100%;
    z-index: 100;

    &:before {
        content: " ";
        display: table;
    }

    &:after {
        content: " ";
        display: table;
        clear: both;
    }
}

.edgtf-container-inner { //not in parent selector since this class is used for positioning other elements in grid
    width: $grid-width;
    margin: 0 auto;
    box-sizing: border-box;
}

.edgtf-full-width {
    position: relative;
    z-index: 100;
}

.edgtf-content .edgtf-content-inner > .edgtf-container > .edgtf-container-inner,
.edgtf-content .edgtf-content-inner > .edgtf-full-width > .edgtf-full-width-inner {
	padding-top: 40px; // default value for content top padding
    padding-bottom: 40px; // default value for content bottom padding
}

.edgtf-row-with-shadow {
    position: relative;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.35);
    z-index: 50;
}

.elementor-widget-container > textarea:read-only {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: top;
    margin: 0 0 20px;
    padding: 24px 20px;
    font-family: inherit;
    font-size: 14px;
    line-height: 24px;
    font-weight: inherit;
    color: #fff;
    background-color: transparent;
    border: 1px solid #e1e1e1;
    border-radius: 0;
    box-sizing: border-box;
    outline: 0;
    cursor: pointer;
}

section.edgtf-elementor-row-grid-section {

    > .elementor-container {
        width: $grid-width;
        margin: 0 auto;
    }
}

