/* #General styles that are used in our theme - end
   ========================================================================== */

@include keyframes(preload-background) {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 100% 0;
    }
}

.edgtf-preload-background {
    background: transparent url('../img/preload_pattern.png') repeat scroll inherit !important;
    @include animation(preload-background, 40s, infinite, linear, 0s);
}

.edgtf-grid {
    width: $grid-width;
    margin: 0 auto;
}

.edgtf-vertical-align-containers {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;

    &.edgtf-25-50-25 {

        .edgtf-position-left,
        .edgtf-position-right {
            width: 25%;
        }

        .edgtf-position-center {
            width: 50%;
            left: 50%;
            @include edgtfTransform(translateX(-50%));
        }
    }

    &.edgtf-33-33-33 {

        .edgtf-position-left,
        .edgtf-position-center,
        .edgtf-position-right {
            width: 33.33%;
        }

        .edgtf-position-center {
            width: 50%;
            left: 50%;
            @include edgtfTransform(translateX(-50%));
        }
    }

    &.edgtf-50-50 {

        .edgtf-position-left,
        .edgtf-position-right {
            width: 50%;
        }
    }

    &.edgtf-33-66 {
        
        .edgtf-position-left {
            width: 33.33%;
        }

        .edgtf-position-right {
            width: 66.66%;
        }
    }

    &.edgtf-66-33 {
        
        .edgtf-position-left {
            width: 66.66%;
        }

        .edgtf-position-right {
            width: 33.33%;
        }
    }

    .edgtf-position-left {
        position: relative;
        float: left;
        z-index: 2;
        height: 100%;
    }
    
    .edgtf-position-center {
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        text-align: center;
        z-index: 1;
    }

    .edgtf-position-right {
        position: relative;
        float: right;
        z-index: 2;
        height: 100%;
        text-align: right;
    }

    .edgtf-position-left-inner,
    .edgtf-position-center-inner,
    .edgtf-position-right-inner {
        vertical-align: middle;
        display: inline-block;
    }
    
    .edgtf-position-right.edgtf-has-widget .edgtf-position-right-inner {
        height: 100%;
    }

    .edgtf-position-left:before,
    .edgtf-position-center:before,
    .edgtf-position-right:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        height: 100%;
        margin-right: 0; /* adjusts for spacing before it was -3px */
    }
}

