.edgtf-parallax-row-holder {
	position: static;
	padding: 0;
	background-repeat: no-repeat;
	background-color: transparent;
	background-position: center 0;
	background-attachment: fixed;
	overflow: hidden;

    //elementor fix when someone sets background image on section from their options and elementor automatically adds transition to background
    .elementor & {
        transition: none !important;
    }

    &:not(.edgtf-angled-shape-inner) {
        @include ipad-landscape {
            height: auto !important;
            min-height: 200px !important;
            background-position: center top !important;
            background-attachment: scroll;
            background-size: cover;

            &.edgtf-fixed-on-touch {
                background-attachment: fixed!important;
            }
        }
    }

    &.edgtf-angled-shape-inner {
        @include ipad-landscape {
            background-position: center top !important;
            background-size: cover;
        }
    }
}
/* ==========================================================================
   #Row styles - end
   ========================================================================== */

/*
/* ==========================================================================
   #Angled Shape styles - begin
   ========================================================================== */
.vc_row.edgtf-row-has-shape{
    position: relative;
    overflow: visible !important;
    .edgtf-angled-shape{

        &.edgtf-angled-top-bottom{
            position: absolute;
            width: 100%;
            height: 128%;
            top: -14%;
            left: 0;
            outline: 1px solid transparent;
            overflow: hidden;

            &.edgtf-angled-left-to-right{
                @include edgtfTransform(skewY(-5deg));
                .edgtf-angled-shape-inner{
                    @include edgtfTransform(skewY(5deg)  scaleY(1.01));
                }
            }
            &.edgtf-angled-right-to-left{
                @include edgtfTransform(skewY(5deg));
                .edgtf-angled-shape-inner{
                    @include edgtfTransform(skewY(-5deg) scaleY(1.01));
                }
            }

            .edgtf-angled-shape-inner{
                position: absolute;
                width: 100%;
                height: 200%; //so it fills angled parts
                top: -50%;
                left: 0;
                background-size: cover;
                background-repeat: no-repeat;
                background-position: center center;
            }

        }
        &.edgtf-angled-svg{
            position: absolute;
            left: 0;
            z-index: 20;
            display: block;
        }
        &.edgtf-angled-top{
            top: -184px;
        }

        &.edgtf-angled-bottom{
            bottom: -184px;
        }

    }
}

/* ==========================================================================
   #Angled Shape styles - end
   ========================================================================== */

/* ==========================================================================
   #VC Revolution Slider styles - begin
   ========================================================================== */

.edgtf-rev-has-paspartu {
	@include edgtfRelativeHolderLayout();
    height: 100vh;
    margin: 0;
    box-sizing: border-box;
    
    .admin-bar & {
        height: calc(100vh - 32px);
    }
	
	&.edgtf-side-paspartu-disabled {
		padding-left: 0 !important;
		padding-right: 0 !important;
	}
	
	&.edgtf-paspartu-tiny {
