/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    > * {

        &:not(.edgtf-social-icon-widget-holder) {
            background-color: $widget-background-color;
        }
    }

    .edgtf-widget-title-holder {
        margin-bottom: $widget-list-item-padding;
        padding: 13px 20px;
        margin-bottom: 11px;
        background-color: $widget-background-color;

        .edgtf-widget-title {
            color: $widget-heading-color;
            margin: 0;

            &:before {
                content: '';
                display: inline-block;
                vertical-align: middle;
                width: 0;
                height: 0;
                border-top: 10px solid transparent;
                border-left: 10px solid #ff0e1f;
                border-bottom: 10px solid transparent;
                position: relative;
                top: -3px;
                padding-right: 15px;
            }
        }

        .edgtf-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-list-item-padding;
            margin-bottom: -14px;

            li {
                padding-left: 10px!important;
                padding-right: 10px!important;
            }
        }
    }

    &.widget_media_image {
        img {
            display: block;
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        .edgtf-widget-title {
            margin-bottom: 0;
        }

        ul {

            li {
                overflow: hidden;
                padding: 15px 21px 14px;
                border-bottom: 1px solid $default-border-color;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    @include edgtfTransition(padding .2s ease);
                    color: $widget-text-color;
                    font-size: 17px;

                    &:hover {
                        color: #fff;
                    }
                }

                ul li {
                    border-bottom: none;
                    border-top: 1px solid $default-border-color;
                }
            }
        }
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        ul li {
            padding-left: 20px;
            padding-right: 20px;

            &:last-child {
                padding-bottom: 15px;
            }
        }

        .edgtf-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;

            a {
                color: $first-main-color;
            }
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {

        .input-holder {
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: 1.2em;
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include edgtfTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});
                padding: 10px 10px 10px 20px;
                font-family: inherit;
                font-size: 17px;
                line-height: 26px;

                @include edgtfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: transparent;
                color: #fff;
                cursor: pointer;
                font-size: 18px;
                width: $widget-search-form-height;

                @include edgtfTransition(color .2s ease-in-out);

                &:hover {
                    color: $default-text-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    // search post type
    &.edgtf-search-post-type-widget {
        .edgtf-post-type-search-field {
            border: none;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 17px;
            font-weight: 400;
            height: $widget-search-form-height;
            line-height: 1.2em;
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            padding: 0 25px 0 20px;
            width: 100%; /* width of loading icon */

            @include edgtfTransition(color .2s ease-in-out);

            @include edgtfPlaceholder {
                color: $widget-text-color;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: $dark-box-background-color;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: 0;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include edgtfTransition(color .2s ease-in-out);

            option {
            }
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        a {
            border-radius: 2px;
            color: $widget-text-color;
            display: inline-block;
            font-size: 11px !important;
            font-family: $default-text-font;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            text-transform: uppercase;
            margin: 0 7px 7px 0;
            padding: 0 19px;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    &.edgtf-recent-post-widget{
        .edgtf-recent-posts{
            .edgtf-rp-item{
                margin: 0 0 10px;
                padding: $widget-list-item-padding;

                a{
                    display: table;
                    width: 100%;

                    .edgtf-rp-image,
                    .edgtf-rp-title{
                        display: table-cell;
                        vertical-align: middle;
                    }

                    .edgtf-rp-title{
                        @include edgtfTransition(color .2s ease-in-out);
                        font-weight: 600;
                    }

                    .edgtf-rp-image{
                        padding-right: 20px;
                        width: 56px;
                        height: 56px;
                        img{
                            display: block;
                        }
                    }

                    &:hover{
                        .edgtf-rp-title{
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
    }
}
