// widgets spacing
$widget-bottom-margin: 50px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;

// widget color
$widget-background-color: rgba(15,15,15,.8);

// list items dimensions
$widget-list-item-height: 22px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: $default-heading-color;

// links
$widget-link-color: $default-text-color;
$widget-link-hover-color: #fff;

// specific widgets
$widget-search-form-height: 54px;
$widget-tag-cloud-height: 30px;

aside.edgtf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .edgtf-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.edgtf-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-flat';

    .widget.widget_text {
        .textwidget {
            padding: 15px 20px;
        }
    }
}