@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    position: relative;
    display: block;
    width: 100%;
    vertical-align: middle;
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 50px 0 20px;
    }
	
	.edgtf-comment {
        padding: 20px 38px 38px 30px;
        background-color: $dark-box-background-color;
        box-sizing: border-box;
		
		&.edgtf-pingback-comment {
			
			.edgtf-comment-text {
				padding: 0;
			}
		}
	}

    .edgtf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 1px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0;

            .edgtf-comment{
                padding: 20px 38px 38px 116px;
            }
        }
    }

    .edgtf-comments-title {
        margin-bottom: 10px;
        background-color: $dark-box-background-color;
        padding: 35px 30px;

        > * {
            margin: 0;
            position: relative;

            &:before {
                content: '';
                display: inline-block;
                vertical-align: middle;
                width: 0;
                height: 0;
                border-top: 10px solid transparent;
                border-left: 10px solid $first-main-color;
                border-bottom: 10px solid transparent;
                position: relative;
                top: -3px;
                padding-right: 15px;
            }
        }
    }

    .edgtf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 68px;
        height: 68px;
        float: left;

        img {
            display: block;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 89px;
        min-height: 82px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 23px 0 2px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 13px;
            color: #fff;
            font-weight: 700;
            text-transform: uppercase;
            line-height: 1.5em;

            &:hover {
                color: $first-main-color;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            margin: 0;
            font-size: 13px;
        }

        .edgtf-text-holder p {
            margin: 35px 0 7px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 13px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .edgtf-comment-form-inner {
        padding: 50px 0 30px;
    }
    
    .comment-reply-title {
        margin: 0 0 10px;
        position: relative;
        background-color: $dark-box-background-color;
        padding: 35px 30px;

        &:before {
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 0;
            height: 0;
            border-top: 10px solid transparent;
            border-left: 10px solid $first-main-color;
            border-bottom: 10px solid transparent;
            position: relative;
            top: -3px;
            padding-right: 15px;
        }
    }

    .comment-form {
        padding: 30px;
        background-color: $dark-box-background-color;
        box-sizing: border-box;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include phone-landscape {
	
    .edgtf-comment-holder {

        .edgtf-comments-title {
            padding: 35px 20px;
        }

        .edgtf-comment {
            padding: 20px 20px 38px 20px;
        }
	    
        .edgtf-comment-list {
	        
            .children {
                padding: 0;

                .edgtf-comment {
                    padding: 20px 20px 38px 45px;
                }
            }
        }

        .edgtf-comment-text {
	        
            .edgtf-comment-date,
            .edgtf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }

    .edgtf-comment-form {
        .comment-reply-title {
            padding: 35px 20px;
        }
        .comment-form {
            padding: 20px;
        }
    }
}

@include phone-portrait {
	
    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

/* qi addons contact form override */
.qodef-qi-contact-form-7 {

	input[type=date],
	input[type=email],
	input[type=number],
	input[type=password],
	input[type=search],
	input[type=tel],
	input[type=text],
	input[type=url],
	select,
	textarea {
		margin: 0 0 10px;
	}
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

/* Contact Formt 7 ajax loader adjustment after plugin update */
.wpcf7 {

	.ajax-loader {
		width: 16px;
		height: 16px;
		margin: 0 0 0 4px;
	}
}

/* Contact Formt 7 spinner adjustment after plugin update */
.wpcf7-spinner {
	visibility: hidden;
	display: inline-block;
	width: 16px;
	height: 16px;
	border: none;
	padding: 0;
	margin: 0 0 0 4px;
	vertical-align: middle;
	position: absolute;
	right: 0;
	bottom: 0;
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include edgtfButtonDefaultStyle();
	@include edgtfButtonSize();
	@include edgtfButtonSolidColor();
    cursor: pointer;
    @include edgtfTransition(.5s cubic-bezier(0.22, 0.61, 0.36, 1));
	
	&:hover {
        -webkit-filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
        filter: drop-shadow(3px 3px 20px rgba($first-main-color,.7));
	}
}

#submit_comment{
    width: 100%;
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 19px 25px;
    height: 160px;
}

.edgtf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

.edgtf-custom-contact-form {
    input.wpcf7-form-control.wpcf7-submit {
        width: 100%;
    }
}

.playerx-newsletter {
    @include phone-landscape {
        text-align: center;

        .edgtf-column-left {
            display: block;
            width: 100%;
        }
        .edgtf-column-right {
            display: inline-block;
            width: auto;
        }
    }
}

.edgtf-custom-footer-form {
    position: relative;
    width: 50%;
    margin: 0 auto;

    .wpcf7-form-control.wpcf7-text{
        background-color: transparent;
        color: $default-text-color;
        border: none;
        font-size: 15px;
        font-weight: 300;
        line-height: 23px;
        margin-bottom: 0;
        padding: 8px 22px;
        border-bottom: 2px solid;
        border-color: $default-border-color;

        &:focus {
            ~ span.edgtf-cf7-filler {
                width: 100%;
            }
        }
    }

    span.edgtf-cf7-filler {
        width: 0;
        height: 2px;
        background-color: $first-main-color;
        position: absolute;
        bottom: -12px;
        left: 0;
        @include edgtfTransition(width .3s ease-in);
    }

    input.wpcf7-form-control.wpcf7-submit{
        background-color: transparent;
        font-family: "Eleganticons";
        font-size: 15px;
        color: #fff;
        position: absolute;
        right: 21px;
        top: 0;
        padding: 0;
        line-height: 33px;

        &:hover{
            color: $first-main-color;
        }
    }

    .ajax-loader{
        right: 0;
        top: 20px;
        position: absolute;
    }

    @include ipad-portrait {
        width: 75%;
    }

    @include phone-landscape {
        width: 90%;
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.edgtf-slider-is-moving {
		
		.owl-item {
			
			.edgtf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.edgtf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include edgtfTransform(translateY($pos));
            }
        }
    }

    &.edgtf-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			padding: 0;
			margin: 0;
			color: $default-heading-color;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			z-index: 3;
			@include edgtfTransform(translateY(-50%));
			@include edgtfTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 30px;
				line-height: 1;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
		}
		
		.owl-next {
			right: 30px;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 16px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			padding: 0;
			margin: 0 6px;
			background: none;
			border: 0;
			border-radius: 0;
			outline: none;
			-webkit-appearance: none;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 12px;
				height: 12px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid rgba($default-heading-color, .5);
				box-sizing: border-box;
				@include edgtfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: $default-heading-color;
					border-color: $default-heading-color;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .edgtf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edgtf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include edgtfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edgtf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}

@include phone-landscape {

    .edgtf-owl-slider  {

        .owl-nav {

            .owl-next {
                right: 4px;
            }

            .owl-prev {
                left: 4px;
            }
        }
    }
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .edgtf-wrapper {
        padding: 0 !important;
    }
    
    .edgtf-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.edgtf-content {
			height: calc(100vh - 32px);
		}
	}

    .edgtf-content-inner {
	    @include edgtfTableLayout();
    }

    .edgtf-page-not-found {
	    @include edgtfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .edgtf-404-title {
            margin: 0;
        }
	
	    .edgtf-404-subtitle {
            margin: 35px 0 0;
        }
	    
	    .edgtf-404-text {
		    margin: 10px 0 0;
	    }

        .edgtf-btn {
            margin: 30px 0 0;
	
	        &.edgtf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
	
	@include ipad-landscape {
		
		.edgtf-content {
            height: auto;
            min-height: 100vh;
            margin-top: -70px!important; /* 70px is mobile header height */

            .edgtf-content-inner {
                min-height: 100vh;
            }
		}
		
		&.admin-bar {
			
			.edgtf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .edgtf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    width: 69px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include edgtfTransition(all 0.3s ease);
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        margin-left: 15px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        @include edgtfTransition(all 0.3s ease);

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: -15px;
            width: 0;
            height: 0;
            border-top: 39px solid #ff0e1f;
            border-left: 15px solid transparent;
        }

        &:after {
            content: '';
            position: absolute;
            top: 0;
            right: -15px;
            width: 0;
            height: 0;
            border-top: 39px solid #ff0e1f;
            border-right: 15px solid transparent;
        }
    }

    &:hover {
        > span {
            @include edgtfTransform(rotateX(180deg) translateZ(15px));
            
            i {
                @include edgtfTransform(rotateX(180deg));
            }
        }
    }

}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
	
    .edgtf-video-wrap {
	
	    .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}
/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

